import React, { useState } from "react";
import SuperSelect from "../SuperSelect/SuperSelect";
import s from "./Clothing.module.css";
import { Hoodies } from "./Hoodies/Hoodies";
import { Jackets } from "./Jackets/Jackets";
import { SweatersButton } from "./Sweaters/SweatersButton";



export const Clothing = () => {
  //        
  const [showMenu, setShowMenu] = useState(false);

  //          
  const showMenuHandler = () => {
    setShowMenu(true);
  };

  //           
  const hideMenuHandler = () => {
    setShowMenu(false);
  };

   //     href={'#'}
   const linkClickHandler = (event:any) => {
    event.preventDefault();
    showMenuHandler();
  };

  return (
    <div className={s.clothingContainer}>
      <a
        //    onMouseOver    
        onMouseOver={showMenuHandler}
        onClick={linkClickHandler}
        className={s.clothingContainer}
        href={"/#"}
      >
        
      </a>
      {/*           showMenu */}
      {showMenu && (
        <div
          //    onMouseLeave    
          onMouseLeave={hideMenuHandler}
          className={s.menu}
        >
          <Jackets />
          <SweatersButton />
          <Hoodies />
          
        </div>
      )}
    </div>
  );
};
